form{
    margin: 100px 0;
}
label {
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 27px;
}
label +label{
    margin-bottom: 115px;
}

input {
    font-size: 24px;
    text-align: center;
    width: 228px;
    padding: 16px;
    box-sizing: border-box;
    border-radius: 7px;
    margin: 0 12px;
}
form .btn{
    position: relative;
}
form .btn::after{
    content: "";
    display: block;
    position: absolute;
    z-index: 2;
    width: 64px;
    height: 72px;
    background: url(../img/click.png) no-repeat;
    top: 14px;
    right: -82px;
} 
