.modal {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%,0);
    width: 700px;
    height: 800px;
    color: #5b2386;
    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
    border-radius: 30px;
    box-sizing: border-box;
    font-family: "OTEnjoystoriesBA";
}

.modal h2 {
    font-size: 96px;
    line-height: 106px;
    padding-top: 76px;
}

.modal p {
    font-size: 36px;
}

.licat {
    width: 433px;
    height: 337px;
    margin-bottom: 74px;
}
