.loading{
    margin: 110px;
    width: 180px;
    text-align: center;
    animation: rotate_image 1.5s linear ;
}

@keyframes rotate_image{
    100% {
        transform: rotate(360deg);
    }
}

.result{
    margin-bottom: 100px;
}

.result p{
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 86px;
    margin-bottom: 20px;
}

.result strong {
    font-size: 72px;
    width: 228px;
    margin: 0 12px;
}
.btn-white{
    background-color: #fff;
    margin-left: 36px;
}